html {
  font-size: 62.5%;
  scroll-behavior: smooth;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(30deg, #160e22, #2D1D46, #632948); }
  @media (min-width: 1700px) {
    html {
      font-size: 72%; } }

body {
  margin: 0;
  padding: 0;
  font-size: 1.4rem;
  font-family: 'Lato', sans-serif;
  color: #C2AC84;
  scroll-behavior: smooth; }

#root {
  flex-direction: column;
  justify-content: space-around; }

a {
  color: inherit;
  text-decoration: none;
  font-family: "Lato"; }

button {
  font-family: inherit;
  background: none;
  border: none;
  color: inherit;
  font-family: inherit;
  cursor: pointer; }
  button:focus {
    outline: none; }

input {
  font-family: inherit; }

@media (min-width: 992px) {
  body::-webkit-scrollbar {
    position: absolute;
    width: 2px;
    background-color: #000; }
  body::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); }
  body::-webkit-scrollbar-thumb {
    border-radius: .5rem;
    background: linear-gradient(to bottom, #000, #C2AC84); } }

.seo__hidden {
  position: absolute;
  visibility: hidden; }

.window {
  z-index: -2;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  position: absolute;
  overflow: hidden; }

@media (min-width: 768px) {
  #content {
    padding-top: 16%; } }

@media (min-width: 992px) {
  #content {
    padding-top: 8%; } }

.backgroundImg {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%; }

.bg_img {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat; }

.title__lg {
  font-size: 3.2rem;
  font-weight: bold;
  font-family: 'Noto Serif'; }

.text {
  line-height: 1.6;
  font-family: "Noto Serif"; }

.mehrlesen {
  line-height: 1;
  height: 16px;
  display: flex;
  letter-spacing: 1.5px;
  transition: 0.35s all cubic-bezier(0.58, 0.12, 0.17, 0.94);
  align-items: center; }
  .mehrlesen .mehrlesen_text {
    font-weight: 900;
    line-height: 1.6;
    transition: inherit; }
  .mehrlesen .mehrlesen_graph {
    width: 60px;
    overflow: hidden;
    transition: 0.35s all cubic-bezier(0.58, 0.12, 0.17, 0.94); }
    .mehrlesen .mehrlesen_graph svg {
      float: right; }
  .mehrlesen .mehrlesen_arrow {
    margin: auto 0;
    position: relative;
    display: inline-block;
    height: 1px;
    background-color: #C2AC84;
    width: 60px;
    transition: inherit; }
    .mehrlesen .mehrlesen_arrow::after {
      content: '>';
      bottom: -8px;
      right: -4px;
      font-size: 19px;
      position: absolute; }

@media (min-width: 1200px) {
  .transition_container {
    width: 99.8vw; } }

.looping_gallery {
  overflow: hidden; }

.looping_gallery_row {
  display: inline-flex;
  transition-timing-function: linear;
  will-change: transform;
  -webkit-backface-visibility: hidden; }

.looping_gallery_item,
.looping_gallery_item-img {
  height: 17vh; }

.looping_gallery_item {
  display: flex;
  align-items: center;
  position: relative;
  -webkit-backface-visibility: hidden;
  cursor: pointer;
  padding: .8rem 0; }
  .looping_gallery_item::after, .looping_gallery_item::before {
    z-index: 1;
    position: absolute;
    width: 2px;
    height: 100%;
    background-color: #4e1e3d; }
  .looping_gallery_item::after {
    right: 0; }
  .looping_gallery_item::before {
    left: 0; }
  .looping_gallery_item:hover .looping_gallery_item-text {
    opacity: 1; }

.looping_gallery_item-img {
  -webkit-backface-visibility: hidden;
  animation: ofloaded 2s cubic-bezier(0.31, 0.02, 0.34, 0.95);
  transition: all .5s; }
  .looping_gallery_item-img:hover {
    opacity: .15;
    transform: scale(0.9);
    transition: 0.4s all cubic-bezier(0.31, 0.02, 0.34, 0.95); }

@keyframes ofloaded {
  from {
    transform: scale(0.8);
    opacity: 0; }
  to {
    transform: scale(1);
    opacity: 1; } }

.looping_gallery_item-text {
  margin-top: 1rem;
  z-index: -2;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  opacity: 0;
  height: 100%;
  font-size: .7em;
  position: absolute;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1.3px;
  background-image: radial-gradient(transparent 100%, #000 10%); }
  .looping_gallery_item-text h5 {
    font-weight: 500;
    padding: 0 25px;
    font-size: 1.2rem; }
    .looping_gallery_item-text h5 span {
      font-size: .85em;
      padding-top: 10px;
      display: block;
      font-weight: 700;
      color: #C2AC84; }
