html {
  font-size: 62.5%;
  scroll-behavior: smooth;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(30deg, #160e22, #2D1D46, #632948); }
  @media (min-width: 1700px) {
    html {
      font-size: 72%; } }

body {
  margin: 0;
  padding: 0;
  font-size: 1.4rem;
  font-family: 'Lato', sans-serif;
  color: #C2AC84;
  scroll-behavior: smooth; }

#root {
  flex-direction: column;
  justify-content: space-around; }

a {
  color: inherit;
  text-decoration: none;
  font-family: "Lato"; }

button {
  font-family: inherit;
  background: none;
  border: none;
  color: inherit;
  font-family: inherit;
  cursor: pointer; }
  button:focus {
    outline: none; }

input {
  font-family: inherit; }

@media (min-width: 992px) {
  body::-webkit-scrollbar {
    position: absolute;
    width: 2px;
    background-color: #000; }
  body::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); }
  body::-webkit-scrollbar-thumb {
    border-radius: .5rem;
    background: linear-gradient(to bottom, #000, #C2AC84); } }

.seo__hidden {
  position: absolute;
  visibility: hidden; }

.window {
  z-index: -2;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  position: absolute;
  overflow: hidden; }

@media (min-width: 768px) {
  #content {
    padding-top: 16%; } }

@media (min-width: 992px) {
  #content {
    padding-top: 8%; } }

.backgroundImg {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%; }

.bg_img {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat; }

.title__lg {
  font-size: 3.2rem;
  font-weight: bold;
  font-family: 'Noto Serif'; }

.text {
  line-height: 1.6;
  font-family: "Noto Serif"; }

.mehrlesen {
  line-height: 1;
  height: 16px;
  display: flex;
  letter-spacing: 1.5px;
  transition: 0.35s all cubic-bezier(0.58, 0.12, 0.17, 0.94);
  align-items: center; }
  .mehrlesen .mehrlesen_text {
    font-weight: 900;
    line-height: 1.6;
    transition: inherit; }
  .mehrlesen .mehrlesen_graph {
    width: 60px;
    overflow: hidden;
    transition: 0.35s all cubic-bezier(0.58, 0.12, 0.17, 0.94); }
    .mehrlesen .mehrlesen_graph svg {
      float: right; }
  .mehrlesen .mehrlesen_arrow {
    margin: auto 0;
    position: relative;
    display: inline-block;
    height: 1px;
    background-color: #C2AC84;
    width: 60px;
    transition: inherit; }
    .mehrlesen .mehrlesen_arrow::after {
      content: '>';
      bottom: -8px;
      right: -4px;
      font-size: 19px;
      position: absolute; }

.scene-container {
  width: 100%;
  height: 100%;
  bottom: 0;
  display: flex;
  justify-content: center;
  position: absolute;
  align-items: flex-end; }

.card1 {
  top: 50%;
  align-self: center;
  position: absolute;
  border: 8px solid #f0ebc5;
  will-change: transform;
  width: 37vw;
  height: 35vh;
  background: radial-gradient(#fff 30%, #fcfae4 100%);
  animation: callOfLions 10s infinite linear; }
  @media (min-width: 768px) {
    .card1 {
      top: 43%;
      width: 27vw; } }
  @media all and (device-width: 1024px) and (device-height: 768px) and (orientation: landscape) {
    .card1 {
      top: 33%;
      width: 17vw; } }
  @media (min-width: 1200px) {
    .card1 {
      top: 20%;
      height: 60vh;
      width: 17vw; } }
  .card1::after {
    content: '';
    position: absolute;
    width: 173%;
    height: 120%;
    top: 0;
    left: -35%;
    background: transparent url("/portal2.png") no-repeat;
    background-size: 100%;
    background-position: center;
    animation: rotating 5s linear infinite; }

@keyframes rotating {
  0% {
    filter: brightness(1);
    transform: rotate(0deg); }
  50% {
    filter: brightness(1.2);
    transform: rotate(180deg); }
  100% {
    filter: brightness(1);
    transform: rotate(360deg); } }

.card2 {
  will-change: transform;
  background-image: url("/layered-lions.png");
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 160%;
  height: 60%;
  bottom: -10%;
  width: 100%; }
  @media (min-width: 768px) {
    .card2 {
      background-size: 120%; } }
  @media (min-width: 992px) {
    .card2 {
      background-size: contain; } }

@keyframes callOfLions {
  0% {
    background: radial-gradient(#fff 0%, #fcfae4 100%);
    box-shadow: 0 0 2px #fff, 0 0 2px #fff, 0 0 3px yellow, 0 0 4px #fcfae4, 0 0 5px #fcfae4; }
  50% {
    background: radial-gradient(#fff 30%, #fcfae4 100%);
    box-shadow: 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #fff, 0 0 50px #fff, 0 0 60px #fff; }
  100% {
    background: radial-gradient(#fff 0%, #fcfae4 100%);
    box-shadow: 0 0 2px #fff, 0 0 2px #fff, 0 0 3px yellow, 0 0 4px #fcfae4, 0 0 5px #fcfae4; } }

.scene {
  height: 100vh;
  position: relative;
  overflow: hidden;
  background: radial-gradient(ellipse at bottom, #ea526a 0%, #2d1d46 70%, #090a0f 100%); }
  .scene::after {
    content: '';
    position: absolute;
    background: radial-gradient(transparent 0%, #000 250%);
    height: 100%;
    width: 100%;
    pointer-events: none;
    top: 0; }
  .scene::before {
    content: ' ';
    position: absolute;
    background: linear-gradient(to top, #000 20%, transparent);
    height: 20%;
    width: 100%;
    pointer-events: none;
    bottom: 0;
    z-index: 1; }
  .scene canvas {
    background-image: url("/nebula-opt-2.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh !important; }
  .scene .scene-landscape {
    background-image: url("/layered-landscape.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 100%;
    bottom: 0;
    width: 100%;
    position: absolute;
    filter: blur(1px); }
  .scene .scene-lions {
    background-image: url("/layered-lions.png");
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: contain;
    height: 70%;
    bottom: 0;
    width: 100%;
    position: absolute; }
